import { Component, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { v4 as uuidv4 } from 'uuid';
import { Subject } from 'rxjs';

import { IInputFieldSpec, makeValidations } from '../../utils/form-helper';


interface IInputFieldSpecExt extends IInputFieldSpec {
    fieldId: string;
    autocompleteKey?: string;
}

@Component({
    selector: 'polp-bs-prompt-form',
    templateUrl: './prompt-form.component.html',
    styleUrls: ['./prompt-form.component.css']
})
export class PromptFormComponent implements OnInit {

    @Input() autocomplete: string;

    @Input() closeBtnLabel: string;
    @Input() confirmBtnLabel: string;
    @Input() closeBtnClasses: string;
    @Input() confirmBtnClasses: string;

    @Input() title: string;
    @Input() innerBody: string;
    @Input() fields: Array<IInputFieldSpec>;

    @Output() result: Subject<{ [key: string]: any }>;

    extFields: IInputFieldSpecExt[];

    form: FormGroup;

    constructor(private readonly _builder: FormBuilder,
        private readonly _bsModalRef: BsModalRef) {
        this.result = new Subject();
    }

    ngOnInit() {
        this.confirmBtnLabel = this.confirmBtnLabel || 'Confirm';
        this.closeBtnLabel = this.closeBtnLabel || 'Cancel';

        this.confirmBtnClasses = this.confirmBtnClasses || 'btn-primary';
        this.closeBtnClasses = this.closeBtnClasses || 'btn-secondary';

        if (this.autocomplete) {
            this.extFields = this.fields.map(a => {
                const k = `section-polp-bs-prompt-form-${this.autocomplete} ${a.name}`;
                return { ...a, fieldId: uuidv4(), autocompleteKey: k };
            });
        } else {
            this.extFields = this.fields.map(a => {
                return { ...a, fieldId: uuidv4() };
            });
        }

        const a = {};
        this.extFields.forEach(x => {
            a[x.name] = new FormControl(x.value, makeValidations(x.validators));
        });

        this.form = this._builder.group(a);
    }

    close() {
        this.result.next(null);
        this.result.complete();
        this._bsModalRef.hide();
    }

    confirm() {
        this.result.next(this.form.value);
        this.result.complete();
        this._bsModalRef.hide();
    }

}
